import React, { useState, useEffect } from 'react';
import dummydata from '../../utils/data';

import Nav from '../../components/Nav';
import DataTable from '../../components/DataTable';
import Spinner from '../../components/Spinner';

const Admin = () => {

  useEffect(() => {
    handleLoad();
  });

  let [loading, setLoading] = useState(true);
  let [data, setData] = useState(dummydata);
  let [isModalShowed, setisModalShowed] = useState(false);

  const handleLoad = () => {
    setTimeout(() => {
      setLoading(false)
    }, 2000)
  }

  const addRow = (e, row) => {
    e.preventDefault();
    setData([...data, row]);
    toggleCreateRowModal(false);
  }

  const deleteRow = i => {
    let filterData = data.filter((row, idx) => idx !== i);
    setData(filterData);
  }

  const toggleCreateRowModal = bool => {
    setisModalShowed(bool);
  }

  if(loading) {
    return <Spinner />
  } else {
    return(
      <section class="slds-grid slds-wrap">
        <Nav addRow={addRow} isModalShowed={isModalShowed} toggleCreateRowModal={toggleCreateRowModal} />
        <div class="slds-grid slds-wrap slds-p-around_small">
          <div class="slds-col slds-size_12-of-12">
            <DataTable data={data} deleteRow={deleteRow} />
          </div>
        </div>
      </section>
    )
  }
}

export default Admin;